* {box-sizing: border-box;}


.image {
  display: block;
  width: 100%;
  height: auto;
}

.avatar{
  margin-top: 30%;
}

.overlay {
  position: absolute; 
  bottom: 0; 
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5); /* Black see-through */
  color: #f1f1f1; 
  height: 100%;
  width: 100%;
  transition: .5s ease;
  opacity:1;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.buttons{
  display: flex;
  max-width: 100%;
  margin-top: -5%;
  background-color: transparent;
}

.container {
	position: relative;
}

.container .enroll {
	width: 90vw;
	margin: 5vw;
	border-radius: 10px;
	box-shadow: var(--bs);
	background: var(--blue);
	padding: 30px 0;
	text-align: center;
	text-transform: uppercase;
	color: var(--white);
	font-size: 1.2em;
	margin-top: 100px;
}

.container .head {
	width: 90vw;
	margin: 20px 5vw;
	border-bottom: 1px solid #555;
	color: #555;
}

.container .info {
	width: 86vw;
	margin: 0 7vw;
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid #000;
	height: 100px;
	line-height: 100px;
	font-size: 1.1em;
}

.feat {
	/*background: var(--shade);*/
	position: absolute;
	top: 95vw;
	z-index: 50;
	width: 100vw;
	display: flex;
	justify-content: space-around;
}

.feat .list {
	background: var(--white);
	width: 250px;
	text-align: center;
	padding: 20px 0;
	border-radius: 10px;
	box-shadow: var(--bs);
	font-size: .8em;
}

.feat .list .main {
	font-size: 1.2em;
	font-weight: bold;
}
